import React, { useCallback, useEffect, useMemo, useState } from 'react';
import PropTypes from 'prop-types';
import useStyles from './../hooks/useStyles';
import cn from './../theme/className';

export function useIndicatorStyle(scrollerRect, selectedRect) {
	const [style, setStyle] = useState({ left: 0, width: 0 });

	const updateStyle = useCallback(
		() =>
			setStyle(() => ({
				left:
					scrollerRect && selectedRect
						? Math.round(
								selectedRect.left - scrollerRect.left + scrollerRect.scrollLeft,
						  )
						: 0,
				width: selectedRect ? selectedRect.width : 0,
			})),
		[],
	);

	useEffect(() => updateStyle(), [scrollerRect.current, selectedRect.current]);

	return style;
}

const getColorStyles = ({ color, theme }) =>
	(color === 'primary' || color === 'secondary') && {
		background: theme.palette[color].main,
	};

const getBaseStyles = props => ({
	position: 'absolute',
	bottom: '0px',
	height: '2px',
	width: '100%',
	transition: props.theme.transition(),
	willChange: 'left, width',
});

function TabIndicator(props) {
	const { className: classNameProp, color, styles: stylesProp, ...passThru } = props;
	const styles = useStyles([getBaseStyles, getColorStyles], {
		color,
		styles: stylesProp,
	});
	const className = useMemo(() => cn(classNameProp, styles), [classNameProp, styles]);

	return <span className={className} {...passThru} />;
}

TabIndicator.displayName = 'TabIndicator';

TabIndicator.propTypes = {
	className: PropTypes.string,
	/**
	 * @ignore
	 * The color of the tab indicator.
	 */
	color: PropTypes.oneOf(['primary', 'secondary']),
	styles: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
};

export default TabIndicator;



