import React, { useCallback, useContext, useEffect, useMemo, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import warning from 'warning';
import { getNormalizedScrollLeft, detectScrollType } from 'normalize-scroll-left';
import { animated, useSpring } from 'react-spring/hooks';
import useDidMount from './../hooks/useDidMount';
import useDidUpdate from './../hooks/useDidUpdate';
import useOnResize from './../hooks/useOnResize';
import usePrevious from './../hooks/usePrevious';
import useScrollbarSize from './../hooks/useScrollbarSize';
import useStyles from './../hooks/useStyles';
import useThrottle from './../hooks/useThrottle';
import useWillUnmount from '../hooks/useWillUnmount';
import Flex from './../Flex';
import TabIndicator, { useIndicatorStyle } from './TabIndicator';
import TabScrollButton, { useShowScrollButtons } from './TabScrollButton';
import cn from './../theme/className';

function useTabsManager(selectedRef, { children, fullWidth, onChange, textColor, value }) {
	const tabs = useMemo(
		() => {
			return React.Children.toArray(children).map((child, idx) => {
				if (!React.isValidElement(child)) return null;
				const childValue = child.props.value ? child.props.value : idx;
				const selected = value === childValue;

				return React.cloneElement(child, {
					fullWidth,
					selected,
					onChange,
					...(selected && { ref: selectedRef }),
					textColor,
					value: childValue,
				});
			});
		},
		[children, selectedRef.current],
	);

	return tabs;
}

function useScrollManager(scroller, selected, props) {
	const [scrollButtons, setScrollButtons] = useState({
		showLeft: false,
		showRight: false,
	});
	const prevShowLeft = usePrevious(scrollButtons.showLeft);
	const prevShowRight = usePrevious(scrollButtons.showRight);
	const [nextScroll, setNextScroll] = useState(0);
	const prevScroll = usePrevious(nextScroll);
	const prevSelected = usePrevious(selected);

	const scrollTo = useCallback(delta => {
		if (!scroller || !selected) return;

		if (delta) {
			setNextScroll(() => scroller.scrollLeft + delta);
		} else if (selected !== prevSelected && selected.left < scroller.left) {
			setNextScroll(() => scroller.scrollLeft + (selected.left - scroller.left));
		} else if (selected !== prevSelected && selected.right > scroller.right) {
			setNextScroll(() => scroller.scrollLeft + (selected.right - scroller.right));
		}
	}, []);

	const handleScroll = useCallback(() => {
		if (props.scrollable || props.scrollButtons !== 'off' || !scroller) {
			const scrollLeft = scroller.normalizedScrollLeft;
			const showLeft = scrollLeft > 0;
			const showRight = scroller.scrollWidth > scroller.clientWidth + scrollLeft;

			if (
				scrollButtons.showLeft !== prevShowLeft ||
				scrollButtons.showRight !== prevShowRight
			) {
				setScrollButtons({
					showLeft,
					showRight,
				});
			}
		}
	}, []);

	useDidMount(() => handleScroll());

	return [
		scrollButtons.showLeft,
		scrollButtons.showRight,
		prevScroll,
		nextScroll,
		scrollTo,
		handleScroll,
	];
}

const getBaseStyles = props => ({
	rootStyles: {
		minHeight: '48px',
		overflow: 'hidden',
		WebkitOverflowScrolling: 'touch', // Adds iOS momentum scrolling.
	},
	indicatorStyles: {},
	scrollButtonStyles: {},
	scrollerStyles: {
		position: 'relative',
		display: 'inline-block',
		flex: '1 1 auto',
		whiteSpace: 'auto',
	},
});

function Tabs(props) {
	const {
		action,
		centered,
		children: childrenProp,
		className: classNameProp,
		component: Component,
		fullWidth,
		indicatorColor,
		onChange,
		scrollable,
		ScrollButtonComponent,
		scrollButtons,
		styles: stylesProp,
		TabIndicatorProps = {},
		textColor,
		value,
		...passThru
	} = props;
	const [scrollerRef, scrollerRect] = useOnResize();
	const [selectedRef, selectedRect] = useOnResize();
	const [scrollbarHeight] = useScrollbarSize();
	const tabs = useTabsManager(selectedRef, props);
	const [
		showScrollLeft,
		showScrollRight,
		prevScroll,
		nextScroll,
		scrollTo,
		handleScroll,
	] = useScrollManager(scrollerRect, selectedRect, props);
	const indicatorStyle = useIndicatorStyle(scrollerRect, selectedRect);
	const { rootStyles, indicatorStyles, scrollButtonStyles, scrollerStyles } = useStyles(
		[getBaseStyles],
		{
			centered,
			fullWidth,
			scrollable,
			styles: stylesProp,
			textColor,
		},
	);
	const className = useMemo(() => cn(classNameProp, rootStyles), [classNameProp, rootStyles]);
	const scrollerClassName = useMemo(() => cn(scrollerStyles), [scrollerStyles]);
	const flexProps = useMemo(() => centered && !scrollable && { justifyContent: 'center' }, []);
	// const [scrollTransition, setScrollTransition] = useSpring(() => ({
	// 	scrollLeft: nextScroll,
	// 	from: {
	// 		scrollLeft: prevScroll,
	// 	},
	// }));

	const handleLeftScrollClick = useCallback(() => scrollTo(-scrollerRef.current.clientWidth), []);

	const handleRightScrollClick = useCallback(() => scrollTo(scrollerRef.current.clientWidth), []);

	// useDidUpdate(
	// 	() => {
	// 		setScrollTransition({
	// 			scrollLeft: nextScroll,
	// 			from: {
	// 				scrollLeft: prevScroll,
	// 			},
	// 		});
	// 	},
	// 	[nextScroll],
	// );

	return (
		<Component className={className} {...passThru}>
			<Flex {...flexProps}>
				{scrollable && (scrollButtons === 'auto' || scrollButtons === 'on') && (
					<ScrollButtonComponent
						direction="left"
						onClick={handleLeftScrollClick}
						visible={showScrollLeft}
						styles={scrollButtonStyles}
					/>
				)}
				<div
					className={scrollerClassName}
					onScroll={handleScroll}
					ref={scrollerRef}
					role="tablist">
					<Flex {...flexProps}>{tabs}</Flex>
					<TabIndicator
						color={indicatorColor}
						{...TabIndicatorProps}
						styles={indicatorStyles}
						style={{ ...TabIndicatorProps.style, ...indicatorStyle }}
					/>
				</div>
				{scrollable && (scrollButtons === 'auto' || scrollButtons === 'on') && (
					<ScrollButtonComponent
						direction="right"
						onClick={handleRightScrollClick}
						visible={showScrollRight}
						styles={scrollButtonStyles}
					/>
				)}
			</Flex>
		</Component>
	);
}

Tabs.displayName = 'Tabs';

Tabs.propTypes = {
	/**
	 * Callback fired when the component mounts.
	 * This is useful when you want to trigger an action programmatically.
	 * It currently only supports `updateIndicator()` action.
	 *
	 * @param {object} actions This object contains all possible actions
	 * that can be triggered programmatically.
	 */
	action: PropTypes.func,
	/**
	 * If `true`, the tabs will be centered.
	 * This property is intended for large views.
	 */
	centered: PropTypes.bool,
	/**
	 * The content of the component.
	 */
	children: PropTypes.node,
	/**
	 * @ignore
	 */
	className: PropTypes.string,
	/**
	 * The component used for the root node.
	 * Either a string to use a DOM element or a component.
	 */
	component: PropTypes.oneOfType([PropTypes.string, PropTypes.func, PropTypes.object]),
	/**
	 * If `true`, the tabs will grow to use all the available space.
	 * This property is intended for small views, like on mobile.
	 */
	fullWidth: PropTypes.bool,
	/**
	 * Determines the color of the indicator.
	 */
	indicatorColor: PropTypes.oneOf(['secondary', 'primary']),
	/**
	 * Callback fired when the value changes.
	 *
	 * @param {object} event The event source of the callback
	 * @param {number} value We default to the index of the child
	 */
	onChange: PropTypes.func,
	/**
	 * True invokes scrolling properties and allow for horizontally scrolling
	 * (or swiping) the tab bar.
	 */
	scrollable: PropTypes.bool,
	/**
	 * The component used to render the scroll buttons.
	 */
	ScrollButtonComponent: PropTypes.oneOfType([
		PropTypes.string,
		PropTypes.func,
		PropTypes.object,
	]),
	/**
	 * Determine behavior of scroll buttons when tabs are set to scroll
	 * `auto` will only present them on medium and larger viewports
	 * `on` will always present them
	 * `off` will never present them
	 */
	scrollButtons: PropTypes.oneOf(['auto', 'on', 'off']),
	styles: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
	/**
	 * Properties applied to the `TabIndicator` element.
	 */
	TabIndicatorProps: PropTypes.object,
	/**
	 * Determines the color of the `Tab`.
	 */
	textColor: PropTypes.oneOf(['secondary', 'primary', 'inherit']),
	/**
	 * The value of the currently selected `Tab`.
	 * If you don't want any selected `Tab`, you can set this property to `false`.
	 */
	value: PropTypes.any,
};

Tabs.defaultProps = {
	centered: false,
	component: 'div',
	fullWidth: false,
	indicatorColor: 'secondary',
	scrollable: false,
	ScrollButtonComponent: TabScrollButton,
	scrollButtons: 'auto',
	textColor: 'inherit',
};

export default Tabs;



