import React, { useCallback, useContext, useMemo, useRef, useState } from 'react';
import PropTypes from 'prop-types';
import useDidMount from './../hooks/useDidMount';
import useDidUpdate from './../hooks/useDidUpdate';
import usePrevious from './../hooks/usePrevious';
import useStyles from './../hooks/useStyles';
import useWillUnmount from '../hooks/useWillUnmount';
import ButtonBase from './../ButtonBase';
import cn from './../theme/className';
import { fontSize, minWidth, space } from './../styles';

const getFullWidthStyles = props =>
	props.fullWidth && {
		rootStyles: {
			maxWidth: 'none',
			flexShrink: 1,
			flexGrow: 1,
		},
	};

const getColorStyles = props => {
	switch (props.color) {
		case 'inherit':
			return {
				rootStyles: {
					color: 'inherit',
					opacity: 0.7,
					...(props.selected && {
						opacity: 1,
					}),
					...(props.disabled && {
						opacity: 0.4,
					}),
				},
			};

		case 'primary':
		case 'secondary':
			return {
				rootStyles: {
					color: props.theme.palette.text.secondary,
					...(props.selected && {
						color: props.theme.palette[props.color].main,
					}),
					...(props.disabled && {
						opacity: props.theme.palette.text.disabled,
					}),
				},
			};

		default:
			return null;
	}
};

const getBaseStyles = props => ({
	rootStyles: {
		...props.theme.typography.variants.button,
		boxSizing: 'border-box',
		position: 'relative',
		maxWidth: '265px',
		minHeight: props.label && props.icon ? '72px' : '48px',
		paddingTop: props.label && props.icon ? '9px' : '0px',
		paddingRight: '0px',
		paddingBottom: '0px',
		paddingLeft: '0px',
		flexShrink: 0,
		overflow: 'hidden',
		whiteSpace: 'normal',
		textAlign: 'center',
		...minWidth({
			wMin: [72, 160],
			theme: props.theme,
		}),
		...fontSize({
			fontSize: [null, props.theme.pxToRem(13)],
		}),
	},
	labelContainerStyles: {
		boxSizing: 'border-box',
		width: '100%', // Fixes an IE 11 issue.
		...space({
			py: 1.5,
			px: [2.5, 3.5],
		}),
	},
	wrapperStyles: {
		width: '100%',
		display: 'inline-flex',
		alignItems: 'center',
		justifyContent: 'center',
		flexDirection: 'column',
	},
});

const Tab = React.forwardRef((props, ref = useRef()) => {
	const {
		disabled,
		fullWidth,
		icon,
		label: labelProp,
		onChange,
		selected,
		styles: stylesProp,
		textColor,
		value,
		...passThru
	} = props;
	const { rootStyles, labelContainerStyles, wrapperStyles } = useStyles(
		[getBaseStyles, getColorStyles, getFullWidthStyles],
		{
			disabled,
			fullWidth,
			icon,
			label: labelProp,
			selected,
			styles: stylesProp,
			textColor,
		},
	);
	const labelContainerClassName = useMemo(() => cn(labelContainerStyles), [labelContainerStyles]);
	const wrapperClassName = useMemo(() => cn(wrapperStyles), [wrapperStyles]);

	let label;
	if (labelProp !== undefined) {
		label = (
			<span className={labelContainerClassName}>
				<span>{labelProp}</span>
			</span>
		);
	}

	const handleChange = useCallback(event => {
		if (onChange) onChange(event, value);
		if (props.onClick) props.onClick(event);
	}, []);

	return (
		<ButtonBase
			focusRipple
			styles={{ rootStyles }}
			role="tab"
			aria-selected={selected}
			disabled={disabled}
			ref={ref}
			{...passThru}
			onClick={handleChange}>
			<span className={wrapperClassName}>{label}</span>
		</ButtonBase>
	);
});

Tab.displayName = 'Tab';

Tab.propTypes = {
	/**
	 * This property isn't supported.
	 * Use the `component` property if you need to change the children structure.
	 */
	children: PropTypes.node,
	/**
	 * @ignore
	 */
	className: PropTypes.string,
	/**
	 * If `true`, the tab will be disabled.
	 */
	disabled: PropTypes.bool,
	/**
	 * @ignore
	 */
	fullWidth: PropTypes.bool,
	/**
	 * The icon element.
	 */
	icon: PropTypes.node,
	/**
	 * @ignore
	 * For server-side rendering consideration, we let the selected tab
	 * render the indicator.
	 */
	indicator: PropTypes.node,
	/**
	 * The label element.
	 */
	label: PropTypes.node,
	/**
	 * @ignore
	 */
	onChange: PropTypes.func,
	/**
	 * @ignore
	 */
	onClick: PropTypes.func,
	/**
	 * @ignore
	 */
	selected: PropTypes.bool,
	styles: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
	/**
	 * @ignore
	 */
	textColor: PropTypes.oneOf(['secondary', 'primary', 'inherit']),
	/**
	 * You can provide your own value. Otherwise, we fallback to the child position index.
	 */
	value: PropTypes.any,
};

Tab.defaultProps = {
	disabled: false,
	textColor: 'inherit',
};

export default Tab;



