import React, {
	Fragment,
	useCallback,
	useContext,
	useMemo,
	useState,
	useRef,
} from 'react';
import useStyles from './../hooks/useStyles';
import AppBar from './../AppBar';
import Box from './../Box';
import DemoBox from './DemoBox';
import Flex from './../Flex';
import Paper from './../Paper';
import Tab from './../Tab';
import Tabs from './../Tabs';
import Toolbar from './../Toolbar';
import Typography from './../Typography';

const getSimpleTabsStyles = props => ({
	paperStyles: {
		rootStyles: {
			width: props.open ? 'calc(100% - 250px)' : '100%',
			marginLeft: props.open ? '250px' : '0px',
			transition: props.theme.transition(
				['margin', 'width'],
				props.open ? 'entering' : 'leaving',
				'sharp',
			),
		},
	},
});

function TabContainer(props) {
	return (
		<Typography component="div" style={{ padding: 8 * 3 }}>
			{props.children}
		</Typography>
	);
}

const SimpleTabs = () => {
	const [value, setValue] = useState(0);
	const { paperStyles } = useStyles([getSimpleTabsStyles], {});

	const handleChange = useCallback(
		(event, value) => setValue(() => value),
		[],
	);

	return (
		<Fragment>
			<DemoBox>
				<Paper elevation={2}>
					<AppBar position="static" styles={{ paperStyles }}>
						<Tabs value={value} onChange={handleChange} scrollable>
							<Tab label="Item One" />
							<Tab label="Item Two" />
							<Tab label="Item Three" />
						</Tabs>
					</AppBar>
					{value === 0 && <TabContainer>Item One</TabContainer>}
					{value === 1 && <TabContainer>Item Two</TabContainer>}
					{value === 2 && <TabContainer>Item Three</TabContainer>}
				</Paper>
			</DemoBox>
		</Fragment>
	);
};

export default () => (
	<Flex w={1}>
		<Box w={1} mt={5.5}>
			<SimpleTabs />
		</Box>
	</Flex>
);



