import React, { useMemo } from 'react';
import PropTypes from 'prop-types';
import useStyles from './../hooks/useStyles';
import ButtonBase from './../ButtonBase';
import KeyboardArrowLeftIcon from './../svgIcons/KeyboardArrowLeft';
import KeyboardArrowRightIcon from './../svgIcons/KeyboardArrowRight';
import cn from './../theme/className';

export function useShowScrollButtons(
	{ clientWidth, normalizedScrollLeft: scrollLeft, scrollWidth },
	scrollable,
	scrollButtons,
) {
	const showScrollLeft = useMemo(
		() => (scrollable && scrollButtons !== 'off' && scrollLeft ? scrollLeft > 0 : false),
		[clientWidth, scrollLeft, scrollWidth],
	);
	const showScrollRight = useMemo(
		() =>
			scrollable && scrollButtons !== 'off' && scrollWidth && clientWidth && scrollLeft
				? scrollWidth > clientWidth + scrollLeft
				: false,
		[clientWidth, scrollLeft, scrollWidth],
	);

	return { showScrollLeft, showScrollRight };
}

const getBaseStyles = {
	color: 'inherit',
	flex: '0 0 56px',
};

function TabScrollButton(props) {
	const {
		className: classNameProp,
		direction,
		onClick,
		styles: stylesProp,
		visible,
		...passThru
	} = props;
	const styles = useStyles([getBaseStyles], { styles: stylesProp });
	const className = useMemo(() => cn(classNameProp, styles), [classNameProp, styles]);

	if (!visible) {
		return <div className={className} />;
	}

	return (
		<ButtonBase className={className} onClick={onClick} tabIndex={-1} {...passThru}>
			{direction === 'left' ? <KeyboardArrowLeftIcon /> : <KeyboardArrowRightIcon />}
		</ButtonBase>
	);
}

TabScrollButton.displayName = 'TabScrollButton';

TabScrollButton.propTypes = {
	/**
	 * @ignore
	 */
	className: PropTypes.string,
	/**
	 * Which direction should the button indicate?
	 */
	direction: PropTypes.oneOf(['left', 'right']),
	/**
	 * Callback to execute for button press.
	 */
	onClick: PropTypes.func,
	styles: PropTypes.oneOfType([PropTypes.func, PropTypes.object]),
	/**
	 * Should the button be present or just consume space.
	 */
	visible: PropTypes.bool,
};

TabScrollButton.defaultProps = {
	visible: true,
};

export default TabScrollButton;



